[data-start] {
  color: #fff;
  cursor: pointer;
  background-color: #14a519;
  border: none;
  border-radius: 10px;
  padding: 20px 40px;
  font-size: 15px;
  transition: all .5s;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .1);
}

[data-start]:hover {
  background-color: #137906;
}

[data-stop] {
  color: #fff;
  cursor: pointer;
  background-color: #bf0a0a;
  border: none;
  border-radius: 10px;
  padding: 20px 40px;
  font-size: 15px;
  transition: all .5s;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, .1);
}

[data-stop]:hover {
  background-color: #8e0303;
}

[data-start]:disabled, [data-stop]:disabled {
  cursor: not-allowed;
  background-color: #979797;
}

